DEFINE_MACRO(COMPARE_TWO_ARRAY, function (m$array1, m$array2) {
  let m$result = true;

  if (m$array1.length == m$array2.length) {
    var m$i = 0, m$I = m$array1.length;
    while (m$i < m$I) {
      if (m$array1[m$i] !== m$array2[m$i]) {
        m$result = false;
        break;
      }
      
      m$i++;
    }
  } else {
    m$result = false;
  }

  return m$result;
});

DEFINE_MACRO(FOR_EACH, function(m$input, m$visitor) {
  let m$i = 0, m$I = m$input.length;
  while (m$i < m$I) {
    m$visitor(m$input[m$i], m$i, m$input);
    m$i++;
  }
});

DEFINE_MACRO(EVERY, function(m$input, m$visitor) {
  let m$result = true;

  let m$i = 0, m$I = m$input.length;
  while (m$i < m$I) {
    if (!m$visitor(m$input[m$i], m$i, m$input)) {
      m$result = false;
      break;
    }

    m$i++;
  }

  return m$result;
});

DEFINE_MACRO(OFFSET_EVERY, function(m$input, m$offse, m$visitor) {
  let m$result = true;

  let m$i = m$offse, m$I = m$input.length;
  while (m$i < m$I) {
    if (!m$visitor(m$input[m$i], m$i, m$input)) {
      m$result = false;
      break;
    }

    m$i++;
  }

  return m$result;
});

DEFINE_MACRO(SOME, function(m$input, m$visitor) {
  let m$result = false;

  let m$i = 0, m$I = m$input.length;
  while (m$i < m$I) {
    if (m$visitor(m$input[m$i], m$i, m$input)) {
      m$result = true;
      break;
    }

    m$i++;
  }

  return m$result;
});
DEFINE_MACRO(REVERSE_SOME, function(m$input, m$visitor) {
  let m$result = false;

  let m$i = m$input.length-1;
  while(m$i > 0) {
    if (m$visitor(m$input[m$i], m$i, m$input)) {
      m$result = true;
      break;
    }

    m$i--;
  }

  return m$result;
});


DEFINE_MACRO(FILTER, function(m$input, m$visitor) {
  const m$filtered = [];

  let m$i = 0, m$I = m$input.length, m$val;
  while (m$i < m$I) {
    m$val = m$input[m$i];
    if (m$visitor(m$val, m$i, m$input)) {
      m$filtered.push(m$val);
    }

    m$i++;
  }

  return m$filtered;
});

DEFINE_MACRO(MAP, (m$input, m$visitor) => {
  const m$length = m$input.length;
  const m$result = new Array(m$length);

  let m$i = 0, m$I = m$input.length;
  while (m$i < m$I) {
    m$result[m$i] = m$visitor(m$input[m$i], m$i, m$input);
    m$i++;
  }
  
  return m$result;
});

DEFINE_MACRO(REDUCE_BY_INITVAL, function(m$input, m$visitor, m$initialValue) {
  let m$result = m$initialValue;

  let m$i = 0, m$I = m$input.length;
  while (m$i < m$I) {
    m$result = m$visitor(m$result, m$input[m$i], m$i, m$input);
    m$i++;
  }

  return m$result;
});


DEFINE_MACRO(MAP_PUSH, function(m$map, m$key, m$newVal) {
  let m$array = m$map.get(m$key);
  if (m$array instanceof Array) {
    m$array.push(m$newVal);
  } else {
    m$map.set(m$key, [m$newVal]);
  }
});

DEFINE_MACRO(BREADTH_FIRST_TRAVERSAL, (m$target, m$depth, m$visitor) => {
  var m$buffer = [[m$target, [], []]];
  var m$tmp, m$node, m$path, m$parent;
  var m$key, m$newNode;
  while (m$buffer.length) {
    m$tmp = m$buffer.shift();
    m$node = m$tmp[0];
    m$path = m$tmp[1];
    m$parent = m$tmp[2];
    if (m$depth === undefined || m$path.length == m$depth) {
      m$visitor(m$node, m$path, m$parent);
    }
    if (typeof m$node == 'object' && (m$depth === undefined || m$path.length < m$depth)) {
      m$parent = m$parent.slice();
      m$parent.push(m$node);
      for (m$key in m$node) {
        m$newNode = m$node[m$key];
        if (m$parent.indexOf(m$newNode) == -1) {
          m$buffer.push([m$newNode, m$path.concat(m$key), m$parent]);
        }
      }
    }
  }
});

DEFINE_MACRO(BREADTH_FIRST_SEARCH, (m$target, m$depth, m$visitor) => {
  var m$result;

  var m$buffer = [[m$target, [], []]];
  var m$tmp, m$node, m$path, m$parent;
  var m$key, m$newNode;
  while (m$buffer.length) {
    m$tmp = m$buffer.shift();
    m$node = m$tmp[0];
    m$path = m$tmp[1];
    m$parent = m$tmp[2];
    if (m$depth === undefined || m$path.length == m$depth) {
      if (m$visitor(m$node, m$path, m$parent)){
        m$result = [m$node, m$path, m$parent];
        break;
      }
    } else if (typeof m$node == 'object' && (m$depth === undefined || m$path.length < m$depth)) {
      m$parent = m$parent.slice();
      m$parent.push(m$node);
      for (m$key in m$node) {
        m$newNode = m$node[m$key];
        if (m$parent.indexOf(m$newNode) == -1) {
          m$buffer.push([m$newNode, m$path.concat(m$key), m$parent]);
        }
      }
    }
  }

  return m$result;
});

DEFINE_MACRO(DEPTH_FIRST_TRAVERSAL, (m$target, m$visitor) => {
  var m$traverse = function m$traverse(m$node, m$path, m$parent) {
    m$visitor(m$node, m$path, m$parent);
    if (typeof m$node == 'object') {
      var m$key, m$newNode;
      m$parent = m$parent.slice();
      m$parent.push(m$node);
      for (m$key in m$node) {
        m$newNode = m$node[m$key];
        if (m$parent.indexOf(m$newNode) == -1) {
          m$traverse(m$newNode, m$path.concat(m$key), m$parent)
        }
      }
    }
  };

  m$traverse(m$target, [], []);
});

DEFINE_MACRO(DEPTH_FIRST_SEARCH, (m$target, m$visitor) => {
  var m$traverse = function m$traverse(m$node, m$path, m$parent) {
    if (m$visitor(m$node, m$path, m$parent)) {
      return [m$node, m$path, m$parent];
    }

    if (typeof m$node == 'object') {
      var m$key, m$newNode, m$tmp;
      m$parent = m$parent.slice();
      m$parent.push(m$node);
      for (m$key in m$node) {
        m$newNode = m$node[m$key];
        if (m$parent.indexOf(m$newNode) == -1) {
          if (m$tmp = m$traverse(m$newNode, m$path.concat(m$key), m$parent)) {
            return m$tmp;
          }
        }
      }
    }
  };

  return m$traverse(m$target, [], []);
});


export function getFromPath(root, path, delimiter='/') {
  if (path.charAt(0) == delimiter) {
    path = path.substr(1);
  }
  
  return getFromPathArray(root, path.split(delimiter));
}

export function getFromPathArray(root, path) {
  var result = root;
  EVERY(path, (val, index, array) => {
    result = result[val];
    return (result !== undefined && result !== null);
  });
  return result;
}

export function listStruct(target, type='bfs', depth) {
  var result = [];
  type = type.toLowerCase();
  if (type == 'bfs') {
    BREADTH_FIRST_TRAVERSAL(target, depth, (val, path, parent)=>{
      result.push([path, parent, val]);
    });
  } else if (type == 'dfs') {
    DEPTH_FIRST_TRAVERSAL(target, (val, path, parent)=>{
      result.push([path, parent, val]);
    });
  }

  return result;
}



