
DEFINE_MACRO(BREADTH_FIRST_TRAVERSAL, (m$target, m$depth, m$visitor) => {
  var m$buffer = [[m$target, [], []]];
  var m$tmp, m$node, m$path, m$parent;
  var m$key, m$newNode;
  while (m$buffer.length) {
    m$tmp = m$buffer.shift();
    m$node = m$tmp[0];
    m$path = m$tmp[1];
    m$parent = m$tmp[2];
    if (m$depth === undefined || m$path.length == m$depth) {
      m$visitor(m$node, m$path, m$parent);
    }
    if (typeof m$node == 'object' && (m$depth === undefined || m$path.length < m$depth)) {
      m$parent = m$parent.slice();
      m$parent.push(m$node);
      for (m$key in m$node) {
        m$newNode = m$node[m$key];
        if (m$parent.indexOf(m$newNode) == -1) {
          m$buffer.push([m$newNode, m$path.concat(m$key), m$parent]);
        }
      }
    }
  }
});

DEFINE_MACRO(BREADTH_FIRST_SEARCH, (m$target, m$depth, m$visitor) => {
  var m$result;

  var m$buffer = [[m$target, [], []]];
  var m$tmp, m$node, m$path, m$parent;
  var m$key, m$newNode;
  while (m$buffer.length) {
    m$tmp = m$buffer.shift();
    m$node = m$tmp[0];
    m$path = m$tmp[1];
    m$parent = m$tmp[2];
    if (m$depth === undefined || m$path.length == m$depth) {
      if (m$visitor(m$node, m$path, m$parent)){
        m$result = [m$node, m$path, m$parent];
        break;
      }
    } else if (typeof m$node == 'object' && (m$depth === undefined || m$path.length < m$depth)) {
      m$parent = m$parent.slice();
      m$parent.push(m$node);
      for (m$key in m$node) {
        m$newNode = m$node[m$key];
        if (m$parent.indexOf(m$newNode) == -1) {
          m$buffer.push([m$newNode, m$path.concat(m$key), m$parent]);
        }
      }
    }
  }

  return m$result;
});

DEFINE_MACRO(DEPTH_FIRST_TRAVERSAL, (m$target, m$visitor) => {
  var m$traverse = function m$traverse(m$node, m$path, m$parent) {
    m$visitor(m$node, m$path, m$parent);
    if (typeof m$node == 'object') {
      var m$key, m$newNode;
      m$parent = m$parent.slice();
      m$parent.push(m$node);
      for (m$key in m$node) {
        m$newNode = m$node[m$key];
        if (m$parent.indexOf(m$newNode) == -1) {
          m$traverse(m$newNode, m$path.concat(m$key), m$parent)
        }
      }
    }
  };

  m$traverse(m$target, [], []);
});

DEFINE_MACRO(DEPTH_FIRST_SEARCH, (m$target, m$visitor) => {
  var m$traverse = function m$traverse(m$node, m$path, m$parent) {
    if (m$visitor(m$node, m$path, m$parent)) {
      return [m$node, m$path, m$parent];
    }

    if (typeof m$node == 'object') {
      var m$key, m$newNode, m$tmp;
      m$parent = m$parent.slice();
      m$parent.push(m$node);
      for (m$key in m$node) {
        m$newNode = m$node[m$key];
        if (m$parent.indexOf(m$newNode) == -1) {
          if (m$tmp = m$traverse(m$newNode, m$path.concat(m$key), m$parent)) {
            return m$tmp;
          }
        }
      }
    }
  };

  return m$traverse(m$target, [], []);
});

import {assert} from 'chai';

describe('traversal', function() {

  describe('BREADTH_FIRST_TRAVERSAL', function () {
    var data = require('./data/testData3.json');
    data.a = data;
    data.menu.popup.menuitem.a = data.menu;
    [
      { 
        input: {
          target: data,
          depth: undefined
        },
        expect: 46
      },
      { 
        input: {
          target: data.menu.id,
          depth: undefined
        },
        expect: 1
      },
      { 
        input: {
          target: data.menu.popup.menuitem[0],
          depth: undefined
        },
        expect: 13
      },
      { 
        input: {
          target: data.menu.popup.menuitem[0],
          depth: 1
        },
        expect: 3
      }
    ].forEach(function (testCase) {
      it(`should traverse over ${testCase.expect} node`, function () {
        var result = 0;

        BREADTH_FIRST_TRAVERSAL(testCase.input.target, testCase.input.depth, (val, path, parent)=>{
          result++;
        });

        assert.strictEqual(result, testCase.expect);
      });
    });
  });

  describe('BREADTH_FIRST_SEARCH', function () {
    var data = require('./data/testData3.json');
    data.a = data;
    data.menu.popup.menuitem.a = data.menu;
    [
      { 
        input: {
          target: data,
          depth: undefined
        },
        targetVal: 'CreateNewDoc()',
        expect: 1
      },
      { 
        input: {
          target: data.menu.id,
          depth: undefined
        },
        targetVal: 'CreateNewDoc',
        expect: 1
      },
      { 
        input: {
          target: data.menu.popup.menuitem[0],
          depth: undefined
        },
        targetVal: 'New2_1',
        expect: 1
      },
      { 
        input: {
          target: data,
          depth: 1
        },
        targetVal: 'CreateNewDoc()',
        expect: 1
      }
    ].forEach(function (testCase) {
      it(`should traverse over ${testCase.expect} node`, function () {
        var result = 0;

        var returnval = BREADTH_FIRST_SEARCH(testCase.input.target, testCase.input.depth, (val, path, parent) => {
          result++;
          return val==testCase.targetVal;
        });

        assert.strictEqual(result, testCase.expect);
      });
    });
  });

  describe('DEPTH_FIRST_TRAVERSAL', function () {
    var data = require('./data/testData3.json');
    data.a = data;
    data.menu.popup.menuitem.a = data.menu;
    [
      { 
        input: data,
        expect: 46
      },
      { 
        input: data.menu.popup.menuitem,
        expect: 45
      },
      { 
        input: data.menu.id,
        expect: 1
      },
      { 
        input: data.menu.popup.menuitem[0],
        expect: 13
      }
    ].forEach(function (testCase) {
      it(`should traverse over ${testCase.expect} node`, function () {
        var result = 0;

        DEPTH_FIRST_TRAVERSAL(testCase.input, (val, path, parent)=>{
          result++;
        });

        assert.strictEqual(result, testCase.expect);
      });
    });
  });

  describe('DEPTH_FIRST_SEARCH', function () {
    var data = require('./data/testData3.json');
    data.a = data;
    data.menu.popup.menuitem.a = data.menu;
    [
      { 
        input: data,
        targetVal: 'CreateNewDoc()',
        expect: 10
      },
      { 
        input: data.menu.popup.menuitem,
        targetVal: 'OpenDoc()',
        expect: 17
      },
      { 
        input: data.menu.id,
        targetVal: 'CreateNewDoc',
        expect: 1
      },
      { 
        input: data.menu.popup.menuitem[0],
        targetVal: 'New2_1',
        expect: 6
      }
    ].forEach(function (testCase) {
      it(`should traverse over ${testCase.expect} node`, function () {
        var result = 0;

        var returnval = DEPTH_FIRST_SEARCH(testCase.input, (val, path, parent) => {
          result++;
          return val==testCase.targetVal;
        });
        
        assert.strictEqual(result, testCase.expect);
      });
    });
  });

});



